import React from 'react';
import './Notes.css';

var Prism = require('prismjs');
var loadLanguages = require('prismjs/components/index.js');

class Notes extends React.Component {
    heading = 0;
    math = 0;
    objects = {math: ['math'], code: ['code', 'language'], image: ['url', 'text'], link: ['url', 'text']};
    object = '';
    display = false;
    key = ''
    html = localStorage.getItem('html') ? localStorage.getItem('html') : '<br>';

    componentDidMount() {
        let iframe = document.getElementById('iframe').contentWindow.document;
        iframe.open();
        let style = `
        <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/katex@0.10.1/dist/katex.min.css" integrity="sha384-dbVIfZGuN1Yq7/1Ocstc1lUEm+AT+/rCkibIcC/OmWo5f0EA48Vf8CytHzGrSwbQ" crossorigin="anonymous">
        <style>

        body {
            margin: 0;
            background-color: #333333;
        }

        #editor {
            width: 16cm;
            padding: 2.5cm;
            margin: 0.3125cm auto;
            background-color: white;
            color: #333333;
            outline: none;
            font-family: -apple-system, sans-serif;
            line-height: 1.5;
            word-break: break-word;
            overflow: hidden;
        }
        
        ::-moz-selection {
            background-color: rgba(68, 136, 255, 0.25);
        }
        
        #editor > :first-child {
            margin-top: 0;
        }
        
        #editor > :last-child {
            margin-bottom: 0;
        }
        
        img {
            width: 10.5cm;
            display: block;
            margin: auto;
        }

        .caption {
            text-align: center;
            margin-top: 0.5em;
        }

        .object[data-object="math"] {
            max-width: 16cm;
            overflow: hidden;
            display: inline-flex;
        }
        
        .object[data-object="math"].display {
            display: block;
        }

        a {
            color: #4488FF;
        }
        
        hr {
            border: 1px solid #333333;
        }
        
        blockquote {
            margin: 1em 0;
            padding: 1em;
            border-left: 2px solid #333333;
        }

        blockquote > :first-child {
            margin-top: 0;
        }
        
        blockquote > :last-child {
            margin-bottom: 0;
        }
        
        ul, ol {
            color: inherit;
        }

        [data-object="code"] {
            background-color: #EEEEEE;
            font-family: 'monaco';
            max-width: 16cm;
            overflow: hidden;
            display: inline-flex;
            color: #0088FF;
        }

        [data-object="code"].display {
            white-space: pre-wrap;
            display: block;
            padding: 8px;
            margin: 1em 0;
        }

        .token.string {
            color: #DD4433;
        }

        .token.keyword {
            color: #4488FF;
        }

        .token.function {
            color: #FF8800;
        }

        .token.class-name {
            color: #119955;
        }

        .token.punctuation {
            color: #999999;
        }

        .token.comment {
            color: #9977BB;
        }

        .token.operator {
            color: #888888;
        }

        table {
            border-collapse: collapse;
            margin: auto;
        }

        td, th {
            border: 2px solid black;
            padding: 8px;
        }
        
        @media print {
            #editor {
                margin: 0;
                padding: 0;
                color-adjust: exact;
            }
        }

        @page {
            margin: 2.5cm;
        }

        </style>
        `
        iframe.write(style + '<div id="editor" contentEditable="true">' + this.html + '</div>');
        iframe.close();
        document.getElementById('iframe').contentWindow.addEventListener('scroll', (event) => {
            document.getElementById('options').style.transform = 'translateY(-' + iframe.body.scrollTop + 'px)';
        });
        iframe.getElementById('editor').addEventListener('click', (event) => {
            this.selection(event);
        });
        iframe.getElementById('editor').addEventListener('keyup', (event) => {
            this.selection(event);
        });
        iframe.getElementById('editor').addEventListener('mousedown', (event) => {
            this.handleMouseDown(event);
        });
        iframe.execCommand("defaultParagraphSeparator", false, 'p');
        iframe.getElementById('editor').addEventListener('keydown', (event) => {
            this.handleKeyDown(event);
        });
        window.addEventListener('beforeunload', (event) => {
            localStorage.setItem('html',  iframe.getElementById('editor').innerHTML);
        });
        window.addEventListener('keydown', (event) => {
            if (event.metaKey && event.key === 'p') {
                event.preventDefault();
                document.getElementById('iframe').contentWindow.print();
            } else if (event.metaKey && event.key === 's') {
                event.preventDefault();
                let blob = new Blob(['<html><head>' + style + '</head><body><div id="editor">' + iframe.getElementById('editor').innerHTML + '</body></div>'], {type: 'text/html'});
                let a = window.document.createElement('a');
                a.href = window.URL.createObjectURL(blob);
                a.download = 'document.html';
                document.body.appendChild(a);
                a.click();        
                document.body.removeChild(a);
            }
        });
    }

    handleKeyDown(event) {
        let iframe = document.getElementById('iframe').contentWindow.document;
        if (event.metaKey) {
            if ('123456'.indexOf(event.key) !== -1) {
                event.preventDefault();
                iframe.execCommand('heading', false, 'h' + event.key);
            } else if (event.key === 'l') {
                event.preventDefault();
                iframe.execCommand(event.shiftKey ? 'insertOrderedList' : 'insertUnorderedList');
            } else if (event.key === 'r') {
                event.preventDefault();
                iframe.execCommand('insertHorizontalRule');
            } else if (event.key === 's') {
                event.preventDefault();
                iframe.execCommand('strikethrough');
            } else if (event.key === 'b') {
                event.preventDefault();
                iframe.execCommand('bold');
            } else if (event.key === 'i') {
                event.preventDefault();
                iframe.execCommand('italic');
            } else if (event.key === 'j' || event.key === 'g' || event.key === 'h' || event.key === 'm') {
                event.preventDefault();
                if (event.shiftKey) {
                    this.display = true;
                }
                this.object = {j: 'code', m: 'math', h: 'link', g: 'image'}[event.key];
                iframe.execCommand('insertHTML', false, '<span class="selection"></div>');
                document.getElementById('first').dataset.placeholder = this.objects[this.object][0];
                document.getElementById('second').dataset.placeholder = this.objects[this.object][1];
                document.getElementById('options').style.top = iframe.querySelector('.selection').getBoundingClientRect().top + iframe.body.scrollTop + 'px';
                document.getElementById('options').style.transform = 'translateY(-' + iframe.body.scrollTop + 'px)';
                event.preventDefault();
                document.getElementById('options').classList.add('focus');
                document.getElementById('first').innerText = '';
                document.getElementById('first').focus();
            } else if (event.key === 'p') {
                event.preventDefault();
                iframe.execCommand('formatBlock', false, 'p');
            }
        } else if (event.key === 'Tab') {
            event.preventDefault(); 
            if (event.shiftKey) {
                iframe.execCommand('outdent');
            } else {
                iframe.execCommand('indent');
            }
        } else if (event.key === 'Escape') {
            event.preventDefault();
            iframe.getElementById('editor').blur();
        } else if (event.key === 'Backspace') {
            this.key = 'backspace';
        }

        if (event.key !== 'Backspace') {
            this.key = '';
        }
    }

    selection(event) {
        let iframe = document.getElementById('iframe').contentWindow.document;
        let selection = document.getElementById('iframe').contentWindow.getSelection();
        let range = selection.getRangeAt(0);

        if (range.commonAncestorContainer.parentElement.closest('.object')) {
            if (this.key === 'backspace') {
                iframe.execCommand('undo');
            }
            selection.removeAllRanges();

            if (iframe.querySelector('.selection')) {
                iframe.querySelector('.selection').classList.remove('selection');
            }

            range.commonAncestorContainer.parentElement.closest('.object').classList.add('selection');
            document.getElementById('first').innerText = range.commonAncestorContainer.parentElement.closest('.object').dataset.first;
            document.getElementById('second').innerText = range.commonAncestorContainer.parentElement.closest('.object').dataset.second || 'null';
            document.getElementById('options').classList.add('focus');
            document.getElementById('options').style.top = iframe.querySelector('.selection').getBoundingClientRect().top + iframe.body.scrollTop + 'px';
            document.getElementById('options').style.transform = 'translateY(-' + iframe.body.scrollTop + 'px)';
            if (iframe.querySelector('.selection')) {
                let range = iframe.createRange();
                let selection = document.getElementById('iframe').contentWindow.getSelection();
                range.selectNode(iframe.querySelector('.selection'));
                selection.removeAllRanges();
                selection.addRange(range);
            }
            document.getElementById('first').focus();
            
            this.object = range.commonAncestorContainer.parentElement.closest('.object').dataset.object;
            if (range.commonAncestorContainer.parentElement.closest('.object').classList.contains('display')) {
                this.display = true;
                document.getElementById('display').classList.add('display');
            } else {
                this.display = false;
                document.getElementById('display').classList.remove('display');
            }
            document.getElementById('first').dataset.placeholder = this.objects[this.object][0];
            document.getElementById('second').dataset.placeholder = this.objects[this.object][1];
        }
    }
    
    handleInput(event) {
        let iframe = document.getElementById('iframe').contentWindow.document;

        if (event) {
            if (iframe.querySelector('.selection').classList.contains('display')) {
                this.display = true;
            } else {
                this.display = false;
            }
        }

        iframe.getElementById('editor').focus();

        if (this.object === 'image') {
            iframe.execCommand('insertHTML', false, '<div class="selection object" data-object="image" data-first="' + document.getElementById('first').innerText + '"><div class="image"><img src="' + document.getElementById('first').innerText + '"/></div><div class="caption">' + document.getElementById('second').innerText + '</div></div>');
        } else if (this.object === 'link') {
            iframe.execCommand('insertHTML', false, '<span class="selection object" data-object="link" data-first="' + document.getElementById('first').innerText + '" data-second="' + document.getElementById('second').innerText + '"><a href="' + document.getElementById('first').innerText + '">' + document.getElementById('second').innerText + '</a></span>');
        } else if (this.object === 'math') {
            iframe.execCommand('insertHTML', false, '<span class="selection object ' + (this.display ? 'display' : '') + '" spellcheck="false" data-object="math" data-first="' + document.getElementById('first').innerText + '">' + window.katex.renderToString(document.getElementById('first').innerText, {displayMode: this.display, throwOnError: false, errorColor: '#DD4433'}) + '</span>');
        } else if (this.object === 'code') {
            iframe.execCommand('insertHTML', false, '<span class="selection object ' + (this.display ? 'display' : '') + '" spellcheck="false" data-object="code" data-first="' + document.getElementById('first').innerText.replace(/\"/g, '&quot;') + '" data-second="' + document.getElementById('second').innerText + '">' + Prism.highlight(document.getElementById('first').innerText, Prism.languages.javascript, 'javascript') + '</span>');
        }

        document.getElementById('options').style.top = iframe.querySelector('.selection').getBoundingClientRect().top + iframe.body.scrollTop + 'px';
        document.getElementById('options').style.transform = 'translateY(-' + iframe.body.scrollTop + 'px)';
        if (event) {
            event.target.focus();
        } else {
            iframe.getElementById('editor').blur();
        }
        if (iframe.querySelector('.selection')) {
            let range = iframe.createRange();
            let selection = document.getElementById('iframe').contentWindow.getSelection();
            range.selectNode(iframe.querySelector('.selection'));
            selection.removeAllRanges();
            selection.addRange(range);
        }
    }

    handleEscapeKey(event) {
        if (event.key === 'Escape') {
            event.preventDefault();
            if (!(document.getElementById('first').innerText.length > 1) || (this.object !== 'math' && !(document.getElementById('second').innerText.length > 1))) {
                this.delete();
                document.getElementById('options').classList.remove('focus');
            } else {
                let iframe = document.getElementById('iframe').contentWindow.document;
                let range = iframe.createRange();
                let selection = document.getElementById('iframe').contentWindow.getSelection();
                range.selectNode(iframe.querySelector('.selection'));
                selection.removeAllRanges();
                selection.addRange(range);
                iframe.getElementById('editor').focus();
                document.getElementById('options').classList.remove('focus');
                iframe.querySelector('.selection').classList.remove('selection');
            }
        }
    }

    delete(event) {
        let iframe = document.getElementById('iframe').contentWindow.document;
        let range = iframe.createRange();
        let selection = document.getElementById('iframe').contentWindow.getSelection();
        range.selectNode(iframe.querySelector('.selection'));
        selection.removeAllRanges();
        selection.addRange(range);
        iframe.execCommand('delete');
    }

    handleMouseDown(event) {
        if (document.getElementById('options').classList.contains('focus') && !document.getElementById('first').innerText.length) {
            this.delete();
        } else {
            let iframe = document.getElementById('iframe').contentWindow.document;
            document.getElementById('options').classList.remove('focus');
            if (iframe.querySelector('.selection')) {
                iframe.querySelector('.selection').classList.remove('selection');
            }
        }
    }

    toggleDisplay(event) {
        event.target.classList.toggle('display');
        this.display = !this.display;
        this.handleInput();
    }

    render() {
        return (
            <div id="container">
                <div id="options">
                    <div id="first" contentEditable="true" onInput={(event) => this.handleInput(event)} onKeyDown={(event) => this.handleEscapeKey(event)}/>
                    <div id="second" contentEditable="true" onInput={(event) => this.handleInput(event)} onKeyDown={(event) => this.handleEscapeKey(event)}/>
                    <div id="display" onClick={(event) => this.toggleDisplay(event)}><div id="check"/>&nbsp;display</div>
                </div>
                <iframe id="iframe" title="iframe"></iframe>
            </div>
        )
    }
}

export default Notes;

